.ContactNav {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo {
    margin-bottom: 15px;
}

.logo img {
    height: 30px;
}

@media screen and (max-width: 1024px) {
    .ContactNav {
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        width: 200px;
        margin-right: 40px;
    }
        .logo {
            margin-bottom: 0px;
        }
}